import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Slides, LoadingController, Loading } from 'ionic-angular';
import { MenuController } from 'ionic-angular';
import { ActivityServiceProvider } from '../../providers/activity-service/activity-service';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { NotificationProvider } from '../../providers/notification/notification';

/**
 * Generated class for the ActivitiesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-activities',
  templateUrl: 'activities.html',
})
export class ActivitiesPage {
  @ViewChild(Slides) slides: Slides;
  loading: Loading;
  activityList: any = []; // for storing activity list data
  displayPagination: boolean = true; // slider pagination bullets
  currencySymbol: any = this.hotelProvider.objHotel.currency.signo; // currency symbol
  activeSlider: number;

  constructor(public navCtrl: NavController, public navParams: NavParams, private activityProvider: ActivityServiceProvider, private hotelProvider: HotelServiceProvider, public menuCtrl: MenuController, public loadingCtrl: LoadingController,private notificationProvider: NotificationProvider) {
    //set previously viewed slider
    if (this.activityProvider.objActivity && this.activityProvider.objActivity.activeSlider) {
      this.activeSlider = this.activityProvider.objActivity.activeSlider;
    }
    //get activity list
    this.activityList=this.activityProvider.objActivity.data; 
  }
  ionViewWillLoad(){
  }
  ionViewWillLeave() {
    //save active slider index
    this.activityProvider.objActivity.activeSlider = this.slides.getActiveIndex();
  }

  ionViewDidLoad() {
      
      if (this.activeSlider) {
            setTimeout(() => {
              this.slides.slideTo(this.activeSlider, 0);
            }, 200);
      }
    
  }

  //moves to next slide
  slideNext() {
    this.slides.slideNext();
  }

  //moves to previous slide
  slidePrev() {
    this.slides.slidePrev();
  }

  fnGotoDetailsPage(itemId) {
    this.navCtrl.push("ActivitiesDetailsPage", { itemId: itemId }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
  }

  // slide changed
  slideChanged() {
    // add class if last slide
    if (this.slides.isEnd()) {
      document.getElementById("headerNav").classList.add('header_menu');
    } else { // remove class
      document.getElementById("headerNav").classList.remove('header_menu');
    }

  }

  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

}



